#section-title {
  margin-top: 120px;

  .imagine-link {
    margin-left: 40px;
    font-weight: 600;
    font-family: $font-family-body; }

  .tringle-space {
    padding-right: 4px; }

  .counter-count {
    color: #FF7F57; }

  .title-head {
    margin-bottom: 20px; }

  .title-down-text {
    margin: 32px 0;
    display: flex;
    max-width: 735px;
    position: relative; }

  .line {
    background: linear-gradient(38.44deg, #F47F57 15.81%, #FD5656 87.57%);
    width: 4px;
    display: flex;
    position: absolute;
    height: 100%;
    margin-top: 0;
    margin-bottom: 0; }

  .text-slide-right {
    overflow: hidden;
    line-height: 24px;
    color: rgba(255, 255, 255, 0.64);
    font-weight: 700;
    width: 735px;
    padding-left: 19px;

    p {
      animation: 3s text-slide-right;
      margin-bottom: 0; } }

  @keyframes text-slide-right {
    from {
      margin-left: -100%;
      width: 100%; } }

  to {
      padding-left: 20px;
      width: 100%; } }


#videoModal {
  top: 10%; }

.modal-dialog {
  margin: 0;
  max-width: 854px;
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  max-width: 854px;
  left: 50%;
  transform: translateX(-50%)!important; }

.modal-content {
  background: none; }

.modal-header {
  border: none;
  padding: 0;
  height: 60px;
  display: inline-block; }

.close-btn {
  width: 50px;
  height: 50px;
  position: relative;
  cursor: pointer;
  float: right; }

.close-btn:before, .close-btn:after {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff; }

.close-btn:after {
  transform: translate(-50%, -50%) rotate(-45deg); }

#video {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }

.video-outer {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative; }

@media (max-width: 740px) {
  .modal-dialog {
    width: 96%; }

  .imagine-link {
    margin-left: 0!important; }

  .imagine-link {
    margin-top: 20px;
    display: inline-block; }

  .sm-center {
    text-align: center; }

  @media screen and (max-width: 575px) {
    #section-title {
      margin-top: 50px; }

    #section-title .text-slide-right {
      line-height: 21px; } } }
