#section-timer {
  min-height: 224px;
  margin: 80px auto;
  text-align: center;
  font-family: $font-family-heading;
  font-weight: bold;
  @media (max-width: 600px) {
    margin: 50px auto; }

  //.action-btn
  //margin-bottom: 12px

  .theme-btn-calendar {
    font-family: $font-family-body !important;
    background: linear-gradient(38.44deg, #F47F57 15.81%, #FD5656 87.57%) !important;
    display: inline-block !important;
    border-radius: 6px !important;
    text-align: center !important;
    padding: 12px 15px !important;
    font-size: 14px !important;
    color: $color-white !important;
    border: 1px solid #EB7254 !important;
    width: 200px; }

  .addeventatc {
    margin-top: 12px; }

  .main-title {
    font-size: 26px;
    line-height: 1.2;
    text-align: center;
    color: rgba(255, 255, 255, 1);
    padding: 0 15px;
    margin: 30px auto;

    @media (max-width: 600px) {
      font-size: 24px; } }

  .title-text {
    font-size: 32px;
    line-height: 1.2;
    color: rgba(255, 255, 255, 1);
    padding: 0 15px;
    margin: 30px auto; }

  .timer_row {
    margin: 0 auto;
    padding: 30px 0;
    text-align: center;
    text-transform: uppercase;

    .timer_box {
      background: #3E3C3C;
      display: inline-block;
      padding: 15px 25px;
      margin: 8px;
      border-radius: 10px;
      width: 132px;
      height: 122px; }

    .timer_number {
      color: #fff;
      font-size: 46px;
      font-family: arial;
      height: 60px; }

    .timer_text {
      color: #969696;
      font-size: 16px;

      @media screen and (max-width: 600px) {
        .timer_box {
          width: 24%;
          height: auto;
          padding: 15px 5px;
          margin: 0px 3px 3px 0px; }

        .timer_number {
          height: 35px;
          font-size: 30px; }

        .timer_text {
          font-size: 12px; } } } } }

#section-timer-second {
  @extend #section-timer; }

#section-timer-height {
  @extend #section-timer;
  min-height: 230px!important; }


#google_add_calendar {
  background: rgba(97, 134, 242, 0.32) none repeat scroll 0% 0%;
  border-radius: 8px;
  font-family: Inter;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.88);
  display: flex;
  flex-direction: row;
  padding: 15px;
  -moz-box-align: center;
  align-items: center;
  justify-content: space-around;
  max-width: 200px;
  cursor: pointer;
  margin: 0 auto 0 auto;
  margin-top: 12px; }
