    position: relative;
    max-width: 100%;
    width: 100%;
    z-index: 10; }

  .dot {
    position: absolute;
    left: 8.1%;
    top: 7.8%;
    width: 9.5%;
    height: 42.3%; }

  .dot-1 {
    width: 9.3%; }

  .dot-2 {
    left: 22.2%;
