    width: 9.3%; }

  .dot-8 {
    left: 82.7%;
    top: 7.8%;
    width: 9.3%; }

  .dot svg {
    width: 50%;
    height: 50%; }

  .draw-line {
    position: absolute; }

  .upper-left-line {
    top: 0;
    left: 0; }

  .lower-left-line {
    left: 0;
    bottom: 0; }

  .upper-right-line {
    position: absolute;
    right: 0;
    top: 0; }

  .lower-right-line {
    right: 0;
    bottom: 0; }

  .dot-animation {
