  .main-section-bottom {
    margin: 35px auto 0;
    max-width: 970px;
    font-family: $font-family-heading;

    font-style: normal;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0.4000000059604645px;
    text-align: center;
    color: #fff;

    .main-section-bottom-bg {
      border-top-right-radius: 25px;
      border-top-left-radius: 25px;
      padding: 40px 64px;
      background: #F47F57;
      line-height: 32px;
      background: linear-gradient(128.31deg, #FF8B71 5.12%, #C33F50 51.15%, #A81E41 83.33%); } }

  .main-heading {
