.typo-syne-h1 {
  font-size: 42px;
  font-weight: 700; }


.typo-syne-h2 {
  font-size: 32px;
  font-weight: 700; }

.typo-syne-h3 {
  font-size: 24px; }

.typo-syne-h4 {
  font-size: 18px; }

.typo-syne-h5 {
  font-size: 16px; }

.typo-syne-h1, .typo-syne-h2, .typo-syne-h3, .typo-syne-h4, .typo-syne-h5 {
  font-family: $font-family-heading; }

.typo-body-large {
  font-size: 18px;
  font-family: $font-family-body; }

.typo-body-medium {
  font-size: 16px;
  font-family: $font-family-body; }

.typo-body-small {
  font-size: 14px;
  font-family: $font-family-body; }

.typo-capital {
  font-size: 12px; }

.typo-inter-h2 {
  font-size: 24px;
  font-family: $font-family-body; }

.typo-inter-h4 {
  font-size: 16px;
  font-family: $font-family-body; }

@media screen and (max-width: 667px) {
  .typo-syne-h1 {
    font-size: 32px; }

  .typo-syne-h2 {
    font-size: 24px; }

  .typo-syne-h3 {
    font-size: 18px; }

  .typo-syne-h4 {
    font-size: 16px; }

  .typo-syne-h5 {
    font-size: 14px; }

  .typo-body-large {
    font-size: 16px; }

  .typo-body-medium {
    font-size: 14px; }

  .typo-body-small {
    font-size: 12px; } }

@media only screen and ( min-width:1024px) and (max-width:1270px ) {

  .typo-syne-h1 {
    font-size: 36px; }

  .typo-syne-h2 {
    font-size: 26px; }

  .typo-syne-h3 {
    font-size: 18px; }

  .typo-syne-h4 {
    font-size: 14px; }

  .typo-syne-h5 {
    font-size: 12px; } }
