#updates-title {
  margin-top: 40px;
  justify-content: center;
  display: grid;

  #update-box {
    border-width: 1px;
    background-color: #2A2827;
    text-align: center;
    border-radius: 60px;
    height: 50px;
    padding: 12px 20px; }

  #new-tag {
    color: #F7653B;
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    padding-right: 12px; }


  #middle-content {
    color: #FFFFFF;
    font-family: Inter;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    padding-right: 16px; }

  #update-icon {
    color: #FFFFFF; } }
