#get-started {
  padding: 0 130px;

  .divider {
    border: 1px solid rgba(255, 255, 255, 0.08); }

  .grid-parent {
    display: grid;
    column-gap: 20px;
    grid-template-columns: 25% 25% 25% 25%;
    margin-bottom: 112px; }

  .grid-title {
    font-family: Syne;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 32px;
    color: rgba(255, 255, 255, 0.88); }

  .grid-content-text {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 135%;
    color: rgba(255, 255, 255, 0.64);
    cursor: pointer;
    text-decoration: none; }

  .orange_text {
    color: #FF7F57; } }

#hero-section {
  color: #fff;
  padding: 0 141px;
  margin-bottom: 410px;
  margin-top: 84px;
  position: relative;

  .hero-text {
    font-family: Syne;
    font-style: normal;
    font-weight: bold;
    font-size: 42px;
    line-height: 44px;
    text-align: center;
    color: rgba(255, 255, 255, 0.88);
    margin-bottom: 8px; }

  .hero-content {
    font-family: Syne;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: rgba(255, 255, 255, 0.64);
    margin-bottom: 14px;
    max-width: 768px;
    margin: 0 auto; }


  .oval-base {
    position: absolute;
    top: -200px;
    z-index: -1; }


  .grp-vector {
    position: absolute;
    top: 290px;
    left: 50%;
    right: 50%;
    transform: translate(-50%, -50%); }

  .mob-hero-bg {
    display: none; } }


@media screen and (max-width: 768px) {
  #get-started {
    padding: 0 16px;

    .grid-parent {
      grid-template-columns: auto;
      margin-bottom: 64px; }

    .grid-title {
      margin-bottom: 24px; }

    .grid-content-text {
      margin-bottom: 20px; } }

  #hero-section {
    padding: 0 16px;
    margin-bottom: 343px;
    margin-top: 48px;
    .oval-base {
      display: none; }
    .grp-vector {
      display: none; }
    .mob-hero-bg {
      display: block;
      position: absolute;
      top: 160px;
      left: 50%;
      right: 50%;
      transform: translate(-50%, -50%); } } }









