#section-community-partners {
  font-family: $font-family-heading;
  margin: 112px 0px 88px 0px;
  background: #171717;
  border-top-left-radius: 55px;
  border-top-right-radius: 55px;
  padding-top: 56px;
  padding-bottom: 10px;

  a:hover {
    text-decoration: none; }

  .main-heading {
    padding-bottom: 68px; }


  @media (max-width: 500px) {
    .main-heading {
      padding-bottom: 25px; }
    ::-webkit-scrollbar {
     width: 0px;
     height: 6px; }

    ::-webkit-scrollbar-thumb {
      background: #171717;
      height: 5px!important;
      border-radius: 10px; }

    ::-webkit-scrollbar-track {
      background: none; }


    .tab-section {
      overflow: scroll;
      margin-bottom: 10px; }

    .partner-logos {
      width: 58px;
      height: 58px; }

    .tab-content .partner-box:nth-child(4n) {
      padding: 17px;
      font-size: 12px;
      min-height: inherit; }

    .tab-content .partner-text {
      line-height: 16px;
      padding-top: 5px; }

    .tab-content .partner-desc {
      padding-left: 15px !important;
      width: 77%!important; }

    .btm-space {
      margin-bottom: 15px; } }

  .btm-space {
    margin-bottom: 20px; }

  .partner-box {
    height: 100%;
    min-height: 140px;
    background: linear-gradient(180deg, #2D2D2D 0%, #212121 100%);
    border-radius: 10px;
    padding: 32px;
    color: $color-white;
    transition: all 0.5s ease; }

  .partner-box:hover {
     box-shadow: 0px 7px 5px #0C0C0C; }

  .partner-box:hover .partner-name {
    color: #FF8B71; }

  .partner-desc {
    display: inline-block;
    vertical-align: top;
    padding-left: 32px;
    width: 65%; }

  .partner-name {
    margin-bottom: 6px;
    color: rgba(255, 255, 255, 0.88); }

  .partner-text {
    color: #A1A1A1;
    line-height: 24px;
    margin-bottom: 0; }


  .arth-gradient-orange-border {
    position: absolute;
    top: -1.5%; } }

.tab-content {
  display: none; }

.tab {
  margin-bottom: 24px;
  width: max-content;

  li {
   color: rgba(255, 255, 255, 0.88);
   display: inline-block;
   padding-right: 20px;
   cursor: pointer; }

  li:hover:after {
    border-radius: 4px 4px 0 0px;
    background: linear-gradient(81.26deg, #F47F57 14.65%, #FD5656 83.88%);
    height: 4px;
    content: '';
    width: 100%;
    display: block;
    margin-top: 8px; }

  .current:after {
    border-radius: 4px 4px 0 0px;
    background: linear-gradient(81.26deg, #F47F57 14.65%, #FD5656 83.88%);
    height: 4px;
    content: '';
    width: 100%;
    display: block;
    margin-top: 8px; } }

.tab-section.scroll:before {
  right: 0;
  width: 60px;
  background: linear-gradient(90deg,hsla(0,0%,100%,0) 0,#171717 80%,#171717);
  opacity: 1;
  content: "";
  display: block;
  position: absolute;
  top: 65px;
  height: 20px;
  bottom: 0;
  z-index: 1;
  right: 0; }

.tab-section.scroll:after {
  left: 0;
  width: 60px;
  background: linear-gradient(270deg,hsla(0,0%,100%,0) 0,#171717 60%,#171717);
  opacity: 1;
  content: "";
  display: block;
  position: absolute;
  top: 65px;
  height: 20px;
  bottom: 0;
  z-index: 1;
  right: 0; }

.tab-content.current {
  display: block; }

