$font-family-heading: 'Syne', sans-serif;
$font-family-body: 'Inter', sans-serif;
$max-width: 1200px;
$color-white: #fff;
$color-black: #000;
$color-white64: rgba(255, 255, 255, 0.64);
$color-white88: rgba(255, 255, 255, 0.88);

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box; }

// @media screen and (max-width: 209px)
//   .mahadao-logo, .two-buttons, .button1, .button2, .right
//     width: 100%

body {
  background-color: #161616;
  font-family: $font-family-body; }

html {
  display: block;
  height: 100%;
  width: 100%;
  overflow-x: hidden; }

body {
  display: block;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  background-color: #161616;
  position: relative; }

/* scrollbar */
::-webkit-scrollbar {
  width: 5px; }
::-webkit-scrollbar-track {
  background: rgba(#000, 0.1); }
::-webkit-scrollbar-thumb {
  background: #FF8B71;
  border-radius: 20px; }
::-webkit-scrollbar-thumb:hover {
  background: #ff5e3c;
  cursor: pointer; }
.scrollbar-hidden::-webkit-scrollbar {
  display: none; }

